interface IEventEndObject {
  [key: string]: string;
}

interface ICommon {
  tranEv: string;
  animEv: string;
  isMS: boolean;
  isMobile: boolean;
  isIOS: boolean;
  isSafari: boolean;
  isAndroid: boolean;
}

export default class Common implements ICommon {
  tranEv: string;
  animEv: string;
  isMS: boolean;
  isMobile: boolean;
  isIOS: boolean;
  isSafari: boolean;
  isAndroid: boolean;

  constructor() {
    const msie: boolean = navigator.userAgent.indexOf('MSIE') !== -1;
    const tradient: boolean = !!navigator.userAgent.match(/Trident.*rv\:11\./);
    const edge: boolean = navigator.userAgent.indexOf('Edge') !== -1;
    const mobRegex: RegExp = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini|CriOS/i;

    this.tranEv = this.transitionEvent();
    this.animEv = this.animationEvent();
    this.isMS = msie || tradient || edge;
    this.isMobile = mobRegex.test(navigator.userAgent);
    this.isIOS = /iPad|iPhone|iPod/.test(navigator.userAgent) && !(window as any).MSStream;
    this.isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);
    this.isAndroid = navigator.userAgent.toLowerCase().indexOf('android') > -1;
  }

  private transitionEvent() {
    const el: HTMLElement = document.createElement('fakeelement');
    const transitions: IEventEndObject = {
      transition: 'transitionend',
      OTransition: 'oTransitionEnd',
      MozTransition: 'transitionend',
      WebkitTransition: 'webkitTransitionEnd',
    };

    let transition = '';

    Object.keys(transitions).forEach((t: any) => {
      if (el.style[t] !== undefined) {
        transition = transitions[t];
      }
    });

    return transition;
  }

  private animationEvent() {
    const el: HTMLElement = document.createElement('fakeelement');
    const animations: IEventEndObject = {
      animation: 'animationend',
      OAnimation: 'oAnimationEnd',
      MozAnimation: 'animationend',
      WebkitAnimation: 'webkitAnimationEnd',
    };

    let animation = '';

    Object.keys(animations).forEach((a: any) => {
      if (el.style[a] !== undefined) {
        animation = animations[a];
      }
    });

    return animation;
  }

  public scrollTo(where: number, timing: number = 500) {
    // remake in vanilla js

    // const top = where - parseFloat($('.header').css('height'));
    // $((iOS) ? "body" : "body, html").stop().animate({ scrollTop: top }, timing, function() {
    //   $(window).scrollTop(top);
    // });

    console.log(where, timing);
  }
}
